<template>
    <div class="container">
        <h4>{{route}} {{origin}} 往 {{destination}}</h4>

        <div class="stop-list">
            <table class="table text-left">
                <thead class="thead-dark">
                    <tr>
                        <th>站數</th>
                        <th>巴士站</th>
                    </tr>
                </thead>
                <tbody>
                    <!--tr v-for="stop in current_route_stop_list" :key="stop.id" @click="stop_click(stop.seq)"-->
                    <tr v-for="stop in route_stop_list" :key="stop.id" @click="stop_seq_clicked = stop.seq; stop_click(stop.seq, stop.stop)" :ref="'stop'+stop.seq">
                        <td class="td-seq">
                            <img src="@/assets/star.png" v-if="!(stop.star)" width="25px" @click="star_click(stop.seq)">
                            <img src="@/assets/starred.png" v-if="stop.star" width="25px" @click="star_click(stop.seq)">
                            <span> {{ stop.seq }}</span><br>
                        </td>
                        <td>
                            <div :class="[stop.seq == stop_seq_clicked ? `font-weight-bold`: '']">{{ stop.name_tc }}</div>
                            
                            <div v-if="stop_seq_clicked == stop.seq">
                                <div v-if="!(stop.eta_1 || stop.eta_2 || stop.eta_3) && eta_loaded">
                                    暫時沒有預定班次
                                </div>
                                <div v-if="stop.eta_1">
                                    <span class="eta-time-diff">{{ stop.eta_1.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_1.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_1.rmk_tc"> ({{ stop.eta_1.rmk_tc }})</span>
                                </div>
                                <div v-if="stop.eta_2">
                                    <span class="eta-time-diff">{{ stop.eta_2.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_2.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_2.rmk_tc"> ({{ stop.eta_2.rmk_tc }})</span>
                                </div>
                                <div v-if="stop.eta_3">
                                    <span class="eta-time-diff">{{ stop.eta_3.time_diff }}</span><span class="eta-time-diff-desc"> 分鐘</span>
                                    <span class="eta-time"> ({{ stop.eta_3.time }})</span>
                                    <span class="eta-rmk" v-if="stop.eta_3.rmk_tc"> ({{ stop.eta_3.rmk_tc }})</span>
                                </div>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import * as util from '../util/util'

export default {
    name: 'Citybus_ETA',
    data(){
        return {
            company: this.$route.params.company,
            route: this.$route.params.route,
            direction: this.$route.params.direction,
            origin: "",
            destination: "",
            stop_list: [],
            route_list: [],
            route_stop_list: [],
            current_route_stop_list: [],
            stop_seq_clicked: 0,
            eta_loaded: false,
        }
    },
    computed: {
        star_image_src: function(){
            return this.current_route_stop_list.map(function(route_stop){
                if (route_stop.star){
                    return "@/assets/star.png";
                }
                else {
                    return "@/assets/star.png";
                }
            });
        }
    },
    mounted() {
        let that = this;
        
        // Load route stop table
        (async () => {
            // Load route destination
            await util.getNWFBCTBRouteList(that.company, that.route);
            if (that.company == 'NWFB'){
                that.route_list = JSON.parse(localStorage.getItem("NWFBRouteList"));
            }
            else if (that.company == 'CTB'){
                that.route_list = JSON.parse(localStorage.getItem("CTBRouteList"));
            }

            let route_list_find = that.route_list.find(route_item => route_item.route == that.route);

            if (route_list_find){
                if (that.direction == "outbound"){
                    that.origin = route_list_find.orig_tc;
                    that.destination = route_list_find.dest_tc;
                }
                else {
                    that.origin = route_list_find.dest_tc;
                    that.destination = route_list_find.orig_tc;
                }
            }

            // Load stop list table
            let temp_route_stop_list = await util.getNWFBCTBRouteStop(that.company, that.route, that.direction);
            temp_route_stop_list = temp_route_stop_list.data;

            let promises = [];
            that.stop_list = [];
            for (let i = 0; i < temp_route_stop_list.length; i++){
                let stop_id = temp_route_stop_list[i].stop;
                promises.push(util.getNWFBCTBStop(stop_id));
            }
            await Promise.all(promises).then((values) => {
                values.forEach((stop) => {
                    that.stop_list.push(stop.data);
                });
            });

            temp_route_stop_list.map((route) => {
                let stop = that.stop_list.find(stop => stop.stop == route.stop);
                if (stop && stop.name_tc){
                    route.name_tc = stop.name_tc;
                }
                return route;
            });
            that.route_stop_list = temp_route_stop_list;

            // if stop seq specified in query string, trigger click of the stop
            let param_seq = that.$route.query.seq;
            console.log("param_seq" + param_seq);
            
            if (param_seq){
                let stop_id = this.get_stop_id_clicked(param_seq);
                console.log(stop_id);
                that.stop_seq_clicked = param_seq;
                that.stop_click(param_seq, stop_id);
                
                // TODO: check :ref="'stop'+stop.seq"
                // scroll to the row
            }
            
            // load star list
            let star_list = JSON.parse(localStorage.getItem("NWFBCTBRouteStopStar"));
            if (star_list){
                that.route_stop_list = that.route_stop_list.map((route) => {
                    let route_dir = route.direction == "outbound" ? 'O' : 'I';
                    let found = star_list.find(star => (star.route == route.route &&
                                                        star.dir == route_dir &&
                                                        star.seq == route.seq));
                    if (found){
                        route.star = true;
                    }
                    else {
                        route.star = false;
                    }

                    return route;
                });
            }
            

        })();

        setInterval(() => { 
            if (that.eta_loaded){
                let stop_id = this.get_stop_id_clicked(that.stop_seq_clicked);
                that.stop_click(that.stop_seq_clicked, stop_id) ;
            }
        }, 10000);

    },
    methods: {
        stop_click: function(stop_seq, stop_id){
            // load ETA
            let dir;
            if (this.direction == "outbound") dir = 'O';
            else if (this.direction == "inbound") dir = 'I';

            (async () => {
                let eta_data = await util.getNWFBCTBETA(this.company, stop_id, this.route);
                eta_data = eta_data.data;
                let eta_1 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == dir &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 1);
                let eta_2 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == dir &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 2);
                let eta_3 = eta_data.find(eta => eta.route == this.route &&
                                                 eta.dir == dir &&
                                                 eta.seq == stop_seq &&
                                                 eta.eta_seq == 3);

                // map eta to route stop list
                this.route_stop_list = this.route_stop_list.map((e) => {
                    if (eta_1){
                        if (eta_1.eta){
                            e.eta_1 = eta_1;
                            e.eta_1.time = util.formatTime(eta_1.eta);
                            e.eta_1.time_diff = util.etaTimeDifference(eta_1.eta);
                        }
                    }
                    if (eta_2){
                        if (eta_2.eta){
                            e.eta_2 = eta_2;
                            e.eta_2.time = util.formatTime(eta_2.eta);
                            e.eta_2.time_diff = util.etaTimeDifference(eta_2.eta);
                        }
                    }
                    if (eta_3){
                        if (eta_3.eta){
                            e.eta_3 = eta_3;
                            e.eta_3.time = util.formatTime(eta_3.eta);
                            e.eta_3.time_diff = util.etaTimeDifference(eta_3.eta);
                        }
                    }

                    return e;
                });

                this.eta_loaded = true;
            })();
        },
        get_stop_id_clicked: function(seq){
            let stop_clicked = this.route_stop_list.find(stop => stop.seq == seq);
            if (stop_clicked && stop_clicked.stop){
                return stop_clicked.stop;
            }
            return 0;
        },
        star_click: function(stop_seq){
            let that = this;
            let dir;

            if (that.direction == "outbound") dir = 'O';
            else if (that.direction == "inbound") dir = 'I';

            // find the stop object
            let stop_item = {
                route: that.route,
                dir: dir,
                seq: stop_seq
            };

            // if found in star list, remove the starred item from the list, and change the image src
            // else add the item to star list, and change the image src

            let star_list = localStorage.getItem("NWFBCTBRouteStopStar");
            star_list = JSON.parse(star_list);

            if (!star_list){
                star_list = [];
            }
            let star_item_index = -1;

            // find the index of object with starred item
            for (let i = 0; i < star_list.length; i++){
                if ((star_list[i]).route == stop_item.route &&
                    (star_list[i]).dir == stop_item.dir &&
                    (star_list[i]).seq == stop_item.seq){
                    star_item_index = i;
                    break;
                }
            }

            // starred --> unstar
            if (star_item_index > -1){
                star_list.splice(star_item_index, 1);

                // update current_route_stop_list star to false
                for (let i = 0; i < this.route_stop_list.length; i++){
                    let item = this.route_stop_list[i];
                    if (item.route == stop_item.route &&
                        item.dir == stop_item.dir && 
                        item.seq == stop_item.seq){

                        (this.route_stop_list[i]).star = false;
                        break;
                    }
                }
            }
            else {  // unstar --> starred
                star_list.push(stop_item);

                // update current_route_stop_list star to true
                // update current_route_stop_list star to false
                for (let i = 0; i < this.route_stop_list.length; i++){
                    let item = this.route_stop_list[i];
                    if (item.route == stop_item.route &&
                        item.dir == stop_item.dir && 
                        item.seq == stop_item.seq){
                        
                        (this.route_stop_list[i]).star = true;
                        break;
                    }
                }
            }

            localStorage.setItem("NWFBCTBRouteStopStar", JSON.stringify(star_list));

        },
    },
}

</script>

<style scoped>
.td-seq {
    width: 100px;
}
</style>
